<template>
  <v-app>
    <v-layout class="content" align-space-around justify-space-around row>
      <v-flex class="left" xs6>
        <div ref="element">
          <div v-if="!this.uploaded">
            <h1 class="display-1 font-weight-bold">Demo使用说明</h1>
            <ol>
              <li>
                <h2 class="title">上传JSON格式文件（或直接使用样例文本）</h2>
                <div class="subheading">
                  JSON文件格式如下：
                  <br>
                  <img src="http://www.pic68.com/uploads/2018/08/1(7).png"></div>
              </li>
              <li>
                <h2 class="title">数据显示</h2>
                <div class="subheading">
                  左侧会显示出标注页面<br>
                  右侧会显示出对应JSON数据
                  <br>
                  <img src="http://www.pic68.com/uploads/2018/08/2(4).png"></div>
              </li>
              <li>
                <h2 class="title">标注</h2>
                <div class="subheading">
                  选择一些文字来标注
                  <br>
                  <img src="https://i.loli.net/2018/08/16/5b751591a98af.gif" alt="1.gif" title="1.gif"/></div>
              </li>
              <li>
                <h2 class="title">连接</h2>
                <div class="subheading">
                  点击两个标注来连接它们
                  <br>
                  <img src="https://i.loli.net/2018/08/16/5b751591c8cc6.gif" alt="2.gif" title="2.gif"/></div>
              </li>
              <li>
                <h2 class="title">删除</h2>
                <div class="subheading">
                  在标注或连接的文字部分右键可以删除对应元素
                </div>
              </li>
              <li>
                <h2 class="title">下载JSON数据</h2>
                <div class="subheading">
                  点击下载即可
                </div>
              </li>
            </ol>
          </div>
        </div>
      </v-flex>
      <v-flex class="right" xs6 ref="right">
        <pre v-if="this.uploaded">
          <code class="" ref="code">
            {{ content }}
          </code>
        </pre>
        <div v-if="!this.uploaded">
          <h1 class="display-1 font-weight-bold">npm包使用说明</h1>
          <h2 class="title">Install</h2>
          在你的Web项目中：
          <pre><code>npm install poplar-annotation</code></pre>
          阅读下面的API Reference来了解具体使用方法
          <h2 class="title">API Reference</h2>
          <a href="https://github.com/synyi/poplar/blob/master/doc/zh.md">中文</a>
          <br>
          <a href="https://github.com/synyi/poplar/blob/master/doc/en.md">English</a>
          <div style="margin-top: 20px">
            <h2 class="title">Need more information?</h2>
            <h2 class="title">Find a bug?</h2>
            <h2 class="title">Need some feature?</h2>
            <h2 class="title">Want to discuss sth with developer?</h2>
            Welcome to view our <a href="https://github.com/synyi/poplar">GitHub</a>.
            And feel free to <a href="https://github.com/synyi/poplar/issues/new">open an issue</a>if you need any help.
          </div>
        </div>
      </v-flex>
    </v-layout>
    <v-layout class="control-panel blue darken-3"
              align-center justify-center row>
      <v-btn v-if="!uploaded" color="info" class="use-default" @click="useDefault">
        使用样例文本
      </v-btn>
      <v-btn color="info" class="upload-button">
        <input v-if="!uploaded" class="upload" type="file" name="" id="" @change="fileupload">
        上传
      </v-btn>
      <v-btn v-if="uploaded" color="info" @click="download">下载</v-btn>
    </v-layout>

    <v-dialog v-model="showLabelCategoriesDialog" persistent max-width="290">
      <v-card>
        <v-card-title>
          <span class="headline">请选择分类</span>
        </v-card-title>
        <v-card-text>
          <v-radio-group v-model="selectedLabelCategory">
            <v-radio v-for="category in labelCategories"
                     :key="category.id"
                     :label="category.text"
                     :value="category.id"></v-radio>
          </v-radio-group>
        </v-card-text>
        <v-card-actions>
          <v-btn color="blue darken-1" flat="flat" @click="showLabelCategoriesDialog = false">
            取消
          </v-btn>
          <v-btn color="green darken-1" flat="flat" @click="addLabel">
            确定
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="showConnectionCategoriesDialog" persistent max-width="290">
      <v-card>
        <v-card-title>
          <span class="headline">请选择分类</span>
        </v-card-title>
        <v-card-text>
          <v-radio-group v-model="selectedConnectionCategory">
            <v-radio v-for="category in connectionCategories"
                     :key="category.id"
                     :label="category.text"
                     :value="category.id"></v-radio>
          </v-radio-group>
        </v-card-text>
        <v-card-actions>
          <v-btn color="blue darken-1" flat="flat" @click="showConnectionCategoriesDialog = false">
            取消
          </v-btn>
          <v-btn color="green darken-1" flat="flat" @click="addConnection">
            确定
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
  import {AddConnectionAction, AddLabelAction, Annotator, JsonDataManager} from "poplar-annotation";
  import {formatJson} from "@/formatJson";

  export default {
    methods: {
      useDefault: function () {
        this.initAnnotationObject("{\"content\":\"北冥有鱼，其名为鲲。鲲之大，不知其几千里也；化而为鸟，其名为鹏。鹏之背，不知其几千里也；怒而飞，其翼若垂天之云。是鸟也，海运则将徙于南冥。南冥者，天池也。《齐谐》者，志怪者也。《谐》之言曰：“鹏之徙于南冥也，水击三千里，抟扶摇而上者九万里，去以六月息者也。”野马也，尘埃也，生物之以息相吹也。天之苍苍，其正色邪？其远而无所至极邪？其视下也，亦若是则已矣。且夫水之积也不厚，则其负大舟也无力。覆杯水于坳堂之上，则芥为之舟，置杯焉则胶，水浅而舟大也。风之积也不厚，则其负大翼也无力。故九万里，则风斯在下矣，而后乃今培风；背负青天，而莫之夭阏者，而后乃今将图南。蜩与学鸠笑之曰：“我决起而飞，抢榆枋而止，时则不至，而控于地而已矣，奚以之九万里而南为？”适莽苍者，三餐而反，腹犹果然；适百里者，宿舂粮；适千里者，三月聚粮。之二虫又何知！小知不及大知，小年不及大年。奚以知其然也？朝菌不知晦朔，蟪蛄不知春秋，此小年也。楚之南有冥灵者，以五百岁为春，五百岁为秋；上古有大椿者，以八千岁为春，八千岁为秋，此大年也。而彭祖乃今以久特闻，众人匹之，不亦悲乎！汤之问棘也是已。穷发之北，有冥海者，天池也。有鱼焉，其广数千里，未有知其修者，其名为鲲。有鸟焉，其名为鹏，背若泰山，翼若垂天之云，抟扶摇羊角而上者九万里，绝云气，负青天，然后图南，且适南冥也。斥鴳笑之曰：“彼且奚适也？我腾跃而上，不过数仞而下，翱翔蓬蒿之间，此亦飞之至也。而彼且奚适也？”此小大之辩也。故夫知效一官，行比一乡，德合一君，而征一国者，其自视也，亦若此矣。而宋荣子犹然笑之。且举世誉之而不加劝，举世非之而不加沮，定乎内外之分，辩乎荣辱之境，斯已矣。彼其于世，未数数然也。虽然，犹有未树也。夫列子御风而行，泠然善也，旬有五日而后反。彼于致福者，未数数然也。此虽免乎行，犹有所待者也。若夫乘天地之正，而御六气之辩，以游无穷者，彼且恶乎待哉？故曰：至人无己，神人无功，圣人无名。\",\"labelCategories\":[{\"id\":\"0\",\"text\":\"名词\",\"color\":\"#eac0a2\",\"border-color\":\"#8c7361\"},{\"id\":\"1\",\"text\":\"动词\",\"color\":\"#619dff\",\"border-color\":\"#3c619d\"},{\"id\":\"2\",\"text\":\"形容词\",\"color\":\"#9d61ff\",\"border-color\":\"#613C9D\"},{\"id\":\"3\",\"text\":\"副词\",\"color\":\"#ff9d61\",\"border-color\":\"#995e3a\"}],\"labels\":[{\"id\":\"0\",\"categoryId\":\"0\",\"startIndex\":0,\"endIndex\":2},{\"id\":\"1\",\"categoryId\":\"0\",\"startIndex\":3,\"endIndex\":4},{\"id\":\"2\",\"categoryId\":\"0\",\"startIndex\":216,\"endIndex\":217},{\"id\":\"3\",\"categoryId\":\"2\",\"startIndex\":217,\"endIndex\":218},{\"id\":\"4\",\"categoryId\":\"0\",\"startIndex\":219,\"endIndex\":220},{\"id\":\"5\",\"categoryId\":\"2\",\"startIndex\":220,\"endIndex\":221},{\"id\":\"6\",\"categoryId\":\"0\",\"startIndex\":32,\"endIndex\":33},{\"id\":\"7\",\"categoryId\":\"1\",\"startIndex\":46,\"endIndex\":47},{\"id\":\"8\",\"categoryId\":\"0\",\"startIndex\":78,\"endIndex\":80},{\"id\":\"9\",\"categoryId\":\"1\",\"startIndex\":64,\"endIndex\":65}],\"connectionCategories\":[{\"id\":\"0\",\"text\":\"修饰\"},{\"id\":\"1\",\"text\":\"限定\"},{\"id\":\"2\",\"text\":\"是...的动作\"}],\"connections\":[{\"id\":\"0\",\"categoryId\":\"2\",\"fromId\":\"7\",\"toId\":\"6\"},{\"id\":\"1\",\"categoryId\":\"0\",\"fromId\":\"3\",\"toId\":\"2\"},{\"id\":\"2\",\"categoryId\":\"0\",\"fromId\":\"5\",\"toId\":\"4\"},{\"id\":\"3\",\"categoryId\":\"2\",\"fromId\":\"9\",\"toId\":\"6\"}]}")
      },
      initAnnotationObject: function (json) {
        for (let child of this.$refs.element.children) {
          this.$refs.element.removeChild(child);
        }
        let dataManager = new JsonDataManager(json);
        this.annotator = new Annotator(dataManager, this.$refs.element);
        for (let catogoryIndex in dataManager.labelCategories) {
          if (this.selectedLabelCategory === -1) {
            this.selectedLabelCategory = catogoryIndex;
          }
          this.labelCategories.push({
            id: catogoryIndex,
            text: dataManager.labelCategories[catogoryIndex].text
          })
        }

        for (let catogoryIndex in dataManager.connectionCategories) {
          if (this.selectedConnectionCategory === -1) {
            this.selectedConnectionCategory = catogoryIndex;
          }
          this.connectionCategories.push({
            id: catogoryIndex,
            text: dataManager.connectionCategories[catogoryIndex].text
          })
        }

        this.annotator.on('textSelected', (selectionInfo) => {
          this.selectionInfo = selectionInfo;
          this.showLabelCategoriesDialog = true;
        });

        this.annotator.on('labelsConnected', (first, second) => {
          this.from = first;
          this.to = second;
          this.showConnectionCategoriesDialog = true;
        });

        this.content = this.getContent();
        this.uploaded = true;
        setTimeout(() => hljs.highlightBlock(this.$refs.code), 500);
      }, fileupload: function (e) {
        let reader = new FileReader();
        reader.readAsText(e.target.files[0]);
        reader.onload = (event) => {
          this.initAnnotationObject(event.target.result);
        }
      },
      addLabel: function () {
        AddLabelAction.emit(this.annotator.store.dataManager.labelCategories[this.selectedLabelCategory],
          this.selectionInfo.startIndex, this.selectionInfo.endIndex);
        this.showLabelCategoriesDialog = false;
        setTimeout(() => {
          this.$refs.code.innerHTML = this.getContent();
          setTimeout(() => hljs.highlightBlock(this.$refs.code), 500);
        }, 1000)
      },
      addConnection: function () {
        AddConnectionAction.emit(this.annotator.store.dataManager.connectionCategories[this.selectedConnectionCategory],
          this.from, this.to);
        this.showConnectionCategoriesDialog = false;
        setTimeout(() => {
          this.$refs.code.innerHTML = this.getContent();
          setTimeout(() => hljs.highlightBlock(this.$refs.code), 500);
        }, 1000)
      },
      download: function () {
        let eleLink = document.createElement('a');
        eleLink.download = 'data.json';
        eleLink.style.display = 'none';
        let blob = new Blob([this.annotator.store.dataManager.json]);
        eleLink.href = URL.createObjectURL(blob);
        document.body.appendChild(eleLink);
        eleLink.click();
        document.body.removeChild(eleLink);
      },
      getContent: function () {
        if (this.annotator.store) {
          console.log(formatJson(this.annotator.store.dataManager.json));
          return formatJson(this.annotator.store.dataManager.json)
        }
        return '';
      }
    },
    data() {
      return {
        uploaded: false,
        annotator: {},

        content: '',

        showLabelCategoriesDialog: false,
        selectedLabelCategory: -1,
        labelCategories: [],

        showConnectionCategoriesDialog: false,
        selectedConnectionCategory: -1,
        connectionCategories: []
      }
    },
    name: 'App'
  }
</script>

<style>
  .left {
    border-right: solid 2px #c4c4c4;
    padding: 4px;
    max-height: calc(100vh - 64px);
    overflow: scroll;
  }

  .right {
    border-left: solid 2px #c4c4c4;
    padding: 4px;
    max-height: calc(100vh - 64px);
    overflow: scroll;
  }

  .control-panel {
    max-height: 64px;
  }

  .upload-button {
    width: 56px;
    padding: 0;
  }

  .upload {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    opacity: 0;
    cursor: pointer;
  }

  img {
    max-width: 100%;
  }

  .left code {
    color: black;
    border: none;
    width: 100%;
    font-size: 11pt;
    background: inherit;
    box-shadow: none;
  }

  .right code {
    width: 100%;
    padding-left: 10px;
    height: 30px;
    font-size: 11pt;
    line-height: 30px;
  }

  .hljs-attr {
    color: #9d61ff;
  }

  .hljs-string {
    color: #ff619d;
  }

  .hljs-number {
    color: #619dff;
  }
</style>



